<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>重置密码</span>
    </div>
    <!-- 表单 -->
    <el-form
      :model="pwdForm"
      :rules="pwdFormRules"
      ref="pwdFormRef"
      label-width="100px"
    >
      <el-form-item label="原密码" prop="old_pwd">
        <el-input v-model="pwdForm.old_pwd" type="password"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="new_pwd">
        <el-input v-model="pwdForm.new_pwd" type="password"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码" prop="re_pwd">
        <el-input v-model="pwdForm.re_pwd" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="updatePwdFn">修改密码</el-button>
        <el-button @click="reSet">重置</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import { updatePwdAPI } from "@/api";
export default {
  name: "UserPwd",
  data() {
    return {
      // 表单的数据对象
      pwdForm: {
        old_pwd: "",
        new_pwd: "",
        re_pwd: "",
      },
      // 表单的验证规则对象
      pwdFormRules: {
        old_pwd: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            pattern: /^\S{6,15}$/,
            message: "密码长度必须是6-15位的非空字符串",
            trigger: "blur",
          },
          { validator: this.pwdOK, trigger: "blur" },
        ],
        new_pwd: [
          { required: true, message: "请输入新密码", trigger: "blur" },
          {
            pattern: /^\S{6,15}$/,
            message: "密码长度必须是6-15位的非空字符串",
            trigger: "blur",
          },
          { validator: this.samePwd, trigger: "blur" },
        ],
        re_pwd: [
          { required: true, message: "请再次确认新密码", trigger: "blur" },
          {
            pattern: /^\S{6,15}$/,
            message: "密码长度必须是6-15位的非空字符串",
            trigger: "blur",
          },
          { validator: this.rePwd, trigger: "blur" },
        ],
      },
    };
  },
  //   =================================================以下是方法部分=================================================

  methods: {
    reSet() {
      this.$refs.pwdFormRef.resetFields();
    },
    updatePwdFn() {
      console.log("提交");
      this.$refs.pwdFormRef.validate(async (valid) => {
        console.log(valid);
        if (!valid) return false;
        const { data: res } = await updatePwdAPI({
          uid: this.$store.state.userInfo.uid,
          pwd: this.pwdForm.new_pwd,
          old_pwd: this.pwdForm.old_pwd,
          re_pwd: this.pwdForm.re_pwd,
        });
        if (res.code !== 0) return this.$message.error("更新密码失败！");

        console.log(res);

        this.$message.success("更新密码成功！请重新登录。");
        // 调用mutation方法,清除token串
        this.$store.commit("updateToken", "");
        // 清除userInfo
        this.$store.commit("updateUserInfo", {});
        //   跳转至登录页面
        this.$router.push("/login");
      });
    },
    // 检测用户密码是否正确
    pwdOK(rule, value, callback) {
      if (value !== this.$store.state.userInfo.pwd) {
        return callback(new Error("原密码错误！"));
      }
      callback();
    },
    // 检测新旧密码是否相同
    samePwd(rule, value, callback) {
      if (value === this.pwdForm.old_pwd) {
        return callback(new Error("新旧密码不能相同！"));
      }
      callback();
    },
    // 检测两次密码是否一致
    rePwd(rule, value, callback) {
      if (value !== this.pwdForm.new_pwd) {
        return callback(new Error("两次密码不相同！"));
      }
      callback();
    },
  },
};
</script>

<style lang="less" scoped>
.el-form {
  width: 500px;
}
</style>
